import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    role: null,
    working: false,
    alert: {
      show: false,
      text: '',
      type: 'info'
    }
  },
  mutations: {
    setRole (state, role) {
      state.role = role
    },
    setWorking (state, working) {
      state.working = working
    },
    setAlert (state, { type, text }) {
      state.alert = {
        show: true,
        type,
        text
      }
    },
    clearAlert (state) {
      state.alert.show = false
    }
  },
  getters: {
    isAllowed: state => {
      const common = ['logout', 'home']
      const others = (name) => name === 'login'
      const fns = {
        'admin' (name) {
          return name !== 'login'
        },
        'externalUser' (name) {
          return [
            ...common,
            'cars'
          ].includes(name)
        },
        'manager' (name) {
          return [
            ...common,
            'cars', 'subjects', 'air_breaks', 'hand_breaks', 'series'
          ].includes(name)
        },
        'serviceTechnician' (name) {
          return [
            ...common,
            'cars', 'defects'
          ].includes(name)
        }
      }

      return fns[state.role] || others
    }
  }
})
